import * as cp from 'child_process';
import { StringDecoder } from 'string_decoder';
import * as which from 'which';
import * as path from 'path';
import * as vscode from 'vscode';
import { ThrottledDelayer } from './utils/async';

const enum Setting {
	Run = 'php.validate.run',
	Enable = 'php.validate.enable',
	ExecutablePath = 'php.validate.executablePath',
}

export class LineDecoder {
	private a: StringDecoder;
	private b: string | null;

	constructor(encoding: BufferEncoding = 'utf8') {
		this.a = new StringDecoder(encoding);
		this.b = null;
	}

	public write(buffer: Buffer): string[] {
		const result: string[] = [];
		const value = this.b
			? this.b + this.a.write(buffer)
			: this.a.write(buffer);

		if (value.length < 1) {
			return result;
		}
		let start = 0;
		let ch: number;
		while (start < value.length && ((ch = value.charCodeAt(start)) === 13 || ch === 10)) {
			start++;
		}
		let idx = start;
		while (idx < value.length) {
			ch = value.charCodeAt(idx);
			if (ch === 13 || ch === 10) {
				result.push(value.substring(start, idx));
				idx++;
				while (idx < value.length && ((ch = value.charCodeAt(idx)) === 13 || ch === 10)) {
					idx++;
				}
				start = idx;
			} else {
				idx++;
			}
		}
		this.b = start < value.length ? value.substr(start) : null;
		return result;
	}

	public end(): string | null {
		return this.b;
	}
}

enum RunTrigger {
	onSave,
	onType
}

namespace RunTrigger {
	export const strings = {
		onSave: 'onSave',
		onType: 'onType'
	};
	export const from = function (value: string): RunTrigger {
		if (value === 'onType') {
			return RunTrigger.onType;
		} else {
			return RunTrigger.onSave;
		}
	};
}

export default class PHPValidationProvider {

	private static a: RegExp = /(?:(?:Parse|Fatal) error): (.*)(?: in )(.*?)(?: on line )(\d+)/;
	private static b: string[] = ['-l', '-n', '-d', 'display_errors=On', '-d', 'log_errors=Off'];
	private static c: string[] = ['-l', '-n', '-d', 'display_errors=On', '-d', 'log_errors=Off', '-f'];

	private d: boolean;
	private f: boolean;
	private g: IPhpConfig | undefined;
	private h: Promise<void>;

	private i: vscode.Disposable | null = null;
	private j?: vscode.DiagnosticCollection;
	private k?: { [key: string]: ThrottledDelayer<void> };

	constructor() {
		this.d = true;
		this.f = false;
		this.h = this.l();
	}

	public activate(subscriptions: vscode.Disposable[]) {
		this.j = vscode.languages.createDiagnosticCollection();
		subscriptions.push(this);
		subscriptions.push(vscode.workspace.onDidChangeConfiguration(() => this.h = this.l()));

		vscode.workspace.onDidOpenTextDocument(this.m, this, subscriptions);
		vscode.workspace.onDidCloseTextDocument((textDocument) => {
			this.j!.delete(textDocument.uri);
			if (this.k) {
				delete this.k[textDocument.uri.toString()];
			}
		}, null, subscriptions);
	}

	public dispose(): void {
		if (this.j) {
			this.j.clear();
			this.j.dispose();
		}
		if (this.i) {
			this.i.dispose();
			this.i = null;
		}
	}

	private async l(): Promise<void> {
		const section = vscode.workspace.getConfiguration();
		const oldExecutable = this.g?.executable;
		this.d = section.get<boolean>(Setting.Enable, true);

		this.g = await getConfig();

		this.k = Object.create(null);
		if (this.f) {
			this.f = oldExecutable === this.g.executable;
		}
		if (this.i) {
			this.i.dispose();
			this.i = null;
		}
		this.j!.clear();
		if (this.d) {
			if (this.g.trigger === RunTrigger.onType) {
				this.i = vscode.workspace.onDidChangeTextDocument((e) => {
					this.m(e.document);
				});
			} else {
				this.i = vscode.workspace.onDidSaveTextDocument(this.m, this);
			}
			// Configuration has changed. Reevaluate all documents.
			vscode.workspace.textDocuments.forEach(this.m, this);
		}
	}

	private async m(textDocument: vscode.TextDocument): Promise<void> {
		await this.h;
		if (textDocument.languageId !== 'php' || this.f || !this.d) {
			return;
		}

		if (vscode.workspace.isTrusted) {
			const key = textDocument.uri.toString();
			let delayer = this.k![key];
			if (!delayer) {
				delayer = new ThrottledDelayer<void>(this.g?.trigger === RunTrigger.onType ? 250 : 0);
				this.k![key] = delayer;
			}
			delayer.trigger(() => this.n(textDocument));
		}
	}

	private n(textDocument: vscode.TextDocument): Promise<void> {
		return new Promise<void>(resolve => {
			const executable = this.g!.executable;
			if (!executable) {
				this.p(vscode.l10n.t("Cannot validate since a PHP installation could not be found. Use the setting 'php.validate.executablePath' to configure the PHP executable."));
				this.f = true;
				resolve();
				return;
			}

			if (!path.isAbsolute(executable)) {
				// executable should either be resolved to an absolute path or undefined.
				// This is just to be sure.
				return;
			}

			const decoder = new LineDecoder();
			const diagnostics: vscode.Diagnostic[] = [];
			const processLine = (line: string) => {
				const matches = line.match(PHPValidationProvider.a);
				if (matches) {
					const message = matches[1];
					const line = parseInt(matches[3]) - 1;
					const diagnostic: vscode.Diagnostic = new vscode.Diagnostic(
						new vscode.Range(line, 0, line, Number.MAX_VALUE),
						message
					);
					diagnostics.push(diagnostic);
				}
			};

			const options = (vscode.workspace.workspaceFolders && vscode.workspace.workspaceFolders[0]) ? { cwd: vscode.workspace.workspaceFolders[0].uri.fsPath } : undefined;
			let args: string[];
			if (this.g!.trigger === RunTrigger.onSave) {
				args = PHPValidationProvider.c.slice(0);
				args.push(textDocument.fileName);
			} else {
				args = PHPValidationProvider.b;
			}
			try {
				const childProcess = cp.spawn(executable, args, options);
				childProcess.on('error', (error: Error) => {
					if (this.f) {
						resolve();
						return;
					}
					this.o(error, executable);
					this.f = true;
					resolve();
				});
				if (childProcess.pid) {
					if (this.g!.trigger === RunTrigger.onType) {
						childProcess.stdin.write(textDocument.getText());
						childProcess.stdin.end();
					}
					childProcess.stdout.on('data', (data: Buffer) => {
						decoder.write(data).forEach(processLine);
					});
					childProcess.stdout.on('end', () => {
						const line = decoder.end();
						if (line) {
							processLine(line);
						}
						this.j!.set(textDocument.uri, diagnostics);
						resolve();
					});
				} else {
					resolve();
				}
			} catch (error) {
				this.o(error, executable);
			}
		});
	}

	private async o(error: any, executable: string): Promise<void> {
		let message: string | null = null;
		if (error.code === 'ENOENT') {
			if (this.g!.executable) {
				message = vscode.l10n.t("Cannot validate since {0} is not a valid php executable. Use the setting 'php.validate.executablePath' to configure the PHP executable.", executable);
			} else {
				message = vscode.l10n.t("Cannot validate since no PHP executable is set. Use the setting 'php.validate.executablePath' to configure the PHP executable.");
			}
		} else {
			message = error.message ? error.message : vscode.l10n.t("Failed to run php using path: {0}. Reason is unknown.", executable);
		}
		if (!message) {
			return;
		}

		return this.p(message);
	}

	private async p(message: string): Promise<void> {
		const openSettings = vscode.l10n.t("Open Settings");
		if (await vscode.window.showInformationMessage(message, openSettings) === openSettings) {
			vscode.commands.executeCommand('workbench.action.openSettings', Setting.ExecutablePath);
		}
	}
}

interface IPhpConfig {
	readonly executable: string | undefined;
	readonly executableIsUserDefined: boolean | undefined;
	readonly trigger: RunTrigger;
}

async function getConfig(): Promise<IPhpConfig> {
	const section = vscode.workspace.getConfiguration();

	let executable: string | undefined;
	let executableIsUserDefined: boolean | undefined;
	const inspect = section.inspect<string>(Setting.ExecutablePath);
	if (inspect && inspect.workspaceValue) {
		executable = inspect.workspaceValue;
		executableIsUserDefined = false;
	} else if (inspect && inspect.globalValue) {
		executable = inspect.globalValue;
		executableIsUserDefined = true;
	} else {
		executable = undefined;
		executableIsUserDefined = undefined;
	}

	if (executable && !path.isAbsolute(executable)) {
		const first = vscode.workspace.workspaceFolders && vscode.workspace.workspaceFolders[0];
		if (first) {
			executable = vscode.Uri.joinPath(first.uri, executable).fsPath;
		} else {
			executable = undefined;
		}
	} else if (!executable) {
		executable = await getPhpPath();
	}

	const trigger = RunTrigger.from(section.get<string>(Setting.Run, RunTrigger.strings.onSave));
	return {
		executable,
		executableIsUserDefined,
		trigger
	};
}

async function getPhpPath(): Promise<string | undefined> {
	try {
		return await which('php');
	} catch (e) {
		return undefined;
	}
}
