export interface ITask<T> {
	(): T;
}

/**
 * A helper to prevent accumulation of sequential async tasks.
 *
 * Imagine a mail man with the sole task of delivering letters. As soon as
 * a letter submitted for delivery, he drives to the destination, delivers it
 * and returns to his base. Imagine that during the trip, N more letters were submitted.
 * When the mail man returns, he picks those N letters and delivers them all in a
 * single trip. Even though N+1 submissions occurred, only 2 deliveries were made.
 *
 * The throttler implements this via the queue() method, by providing it a task
 * factory. Following the example:
 *
 * 		var throttler = new Throttler();
 * 		var letters = [];
 *
 * 		function letterReceived(l) {
 * 			letters.push(l);
 * 			throttler.queue(() => { return makeTheTrip(); });
 * 		}
 */
export class Throttler<T> {

	private a: Promise<T> | null;
	private b: Promise<T> | null;
	private c: ITask<Promise<T>> | null;

	constructor() {
		this.a = null;
		this.b = null;
		this.c = null;
	}

	public queue(promiseFactory: ITask<Promise<T>>): Promise<T> {
		if (this.a) {
			this.c = promiseFactory;

			if (!this.b) {
				const onComplete = () => {
					this.b = null;

					const result = this.queue(this.c!);
					this.c = null;

					return result;
				};

				this.b = new Promise<T>((resolve) => {
					this.a!.then(onComplete, onComplete).then(resolve);
				});
			}

			return new Promise<T>((resolve, reject) => {
				this.b!.then(resolve, reject);
			});
		}

		this.a = promiseFactory();

		return new Promise<T>((resolve, reject) => {
			this.a!.then((result: T) => {
				this.a = null;
				resolve(result);
			}, (err: any) => {
				this.a = null;
				reject(err);
			});
		});
	}
}

/**
 * A helper to delay execution of a task that is being requested often.
 *
 * Following the throttler, now imagine the mail man wants to optimize the number of
 * trips proactively. The trip itself can be long, so the he decides not to make the trip
 * as soon as a letter is submitted. Instead he waits a while, in case more
 * letters are submitted. After said waiting period, if no letters were submitted, he
 * decides to make the trip. Imagine that N more letters were submitted after the first
 * one, all within a short period of time between each other. Even though N+1
 * submissions occurred, only 1 delivery was made.
 *
 * The delayer offers this behavior via the trigger() method, into which both the task
 * to be executed and the waiting period (delay) must be passed in as arguments. Following
 * the example:
 *
 * 		var delayer = new Delayer(WAITING_PERIOD);
 * 		var letters = [];
 *
 * 		function letterReceived(l) {
 * 			letters.push(l);
 * 			delayer.trigger(() => { return makeTheTrip(); });
 * 		}
 */
export class Delayer<T> {

	public defaultDelay: number;
	private a: NodeJS.Timer | null;
	private b: Promise<T> | null;
	private c: ((value: T | PromiseLike<T> | undefined) => void) | null;
	private d: ITask<T> | null;

	constructor(defaultDelay: number) {
		this.defaultDelay = defaultDelay;
		this.a = null;
		this.b = null;
		this.c = null;
		this.d = null;
	}

	public trigger(task: ITask<T>, delay: number = this.defaultDelay): Promise<T> {
		this.d = task;
		this.e();

		if (!this.b) {
			this.b = new Promise<T | undefined>((resolve) => {
				this.c = resolve;
			}).then(() => {
				this.b = null;
				this.c = null;

				const result = this.d!();
				this.d = null;

				return result;
			});
		}

		this.a = setTimeout(() => {
			this.a = null;
			this.c!(undefined);
		}, delay);

		return this.b;
	}

	public isTriggered(): boolean {
		return this.a !== null;
	}

	public cancel(): void {
		this.e();

		if (this.b) {
			this.b = null;
		}
	}

	private e(): void {
		if (this.a !== null) {
			clearTimeout(this.a);
			this.a = null;
		}
	}
}

/**
 * A helper to delay execution of a task that is being requested often, while
 * preventing accumulation of consecutive executions, while the task runs.
 *
 * Simply combine the two mail man strategies from the Throttler and Delayer
 * helpers, for an analogy.
 */
export class ThrottledDelayer<T> extends Delayer<Promise<T>> {

	private f: Throttler<T>;

	constructor(defaultDelay: number) {
		super(defaultDelay);

		this.f = new Throttler<T>();
	}

	public override trigger(promiseFactory: ITask<Promise<T>>, delay?: number): Promise<Promise<T>> {
		return super.trigger(() => this.f.queue(promiseFactory), delay);
	}
}
